.img-userprofile{
    width: 250px;
    height:250px;
}

.img-pu{
    border-radius: 100%;
}

.post-user{
    position: relative;
    p{
        position: absolute;
        width: 250px;
        height:250px;
        text-align: center;
        padding: 33%;
        color: transparent;
        &:hover{
            display:block;
            background-color: rgba(2, 2, 2, 0.445);
            color: #fff;
        }
    }
}
