.img-user-post{
    img{
        margin: 0;
        max-width: 100%;
    }
}
.wo-p{
    padding: 0 !important;
}
.img-user{
    img{
        width:4rem;
        height: 4rem;
        border-radius: 100%;
        margin-right: 2rem;
    }

}
.post-icon{
    i{
        font-size: 3rem;

    }
}

.direct-user{
    color: black;
    &:hover{
        text-decoration: underline;
    }
}

.post-date{

    font-size: 1.5rem;
}

.comments{
    input{
        border:none;
        outline: none;
        width: 80%;
    }
    button{
        border: none;
        outline: none;
        background: none;
        &:hover{
            color: blue;
        }
    }
}

.sticky{
    position: fixed;
    top: 65px;
    left: 63%;
    width: 100%;
}

.img-detail{
    img{
        max-height: 65rem;
        width:auto
    }
}




